.slider{width:100%;display:flex;justify-content:center;margin:50px 0 50px 0}.slider .custom-swiper-slider{width:928px}.slider .custom-swiper-slider .swiper-slide{display:flex;justify-content:center;align-items:center;transition:transform .3s,filter .3s}.slider .custom-swiper-slider .swiper-slide img{border-radius:0;width:275px;height:600px}.slider .custom-swiper-slider .swiper-slide-prev,.slider .custom-swiper-slider .swiper-slide-next{transform:scale(0.8);filter:brightness(50%)}.slider .custom-swiper-slider .swiper-slide-active{transform:scale(1);filter:brightness(100%)}.hearts-container{position:absolute;top:50%;left:50%;width:100%;height:100%;pointer-events:none;display:flex;flex-wrap:wrap;justify-content:center;align-items:center;z-index:2}.hearts-container::before,.hearts-container::after,.hearts-container .heart1,.hearts-container .heart2,.hearts-container .heart3,.hearts-container .heart4,.hearts-container .heart5{content:"❤️";position:absolute;font-size:1.5rem;color:#ff4081;animation:float 2s ease-out infinite;opacity:0;pointer-events:none;transform:scale(1);display:block}.hearts-container::before{left:10%;animation-delay:0s}.hearts-container::after{right:10%;animation-delay:.5s}.hearts-container .heart1{top:15%;left:5%;animation-delay:.3s}.hearts-container .heart2{top:30%;left:20%;animation-delay:.6s}.hearts-container .heart3{top:45%;left:35%;animation-delay:.9s}.hearts-container .heart4{top:60%;left:50%;animation-delay:1.2s}.hearts-container .heart5{top:75%;left:65%;animation-delay:1.5s}@keyframes float{0%{opacity:1;transform:translateY(0) scale(1)}50%{opacity:1;transform:translateY(-50px) scale(1.5)}100%{opacity:0;transform:translateY(-100px) scale(1)}}